
@media screen and (min-width: 320px) and (max-width: 767px) and (orientation: landscape){
html{display:none
}
}
body{font-family:'roboto_condensedregular', 'Helvetica Neue', Helvetica, Arial, sans-serif;margin:0
}
h1{font-size:36px;font-weight:300;width:10em;margin:1em auto;text-align:center;color:#4a4a4a;border:2px solid black
}
h2{font-size:30px;font-weight:300;text-align:center;color:#4a4a4a
}
h3{font-size:24px;font-weight:300;color:#4a4a4a
}
h4{font-size:20px;font-weight:300;color:#4a4a4a
}
a{text-decoration:none
}
.footer{width:100%;float:right;margin-right:0;background:black
}
.social{width:40.67797%;margin:0 auto;padding:1.5em
}
@media (min-width: 649px){
.social{width:23.72881%
}
}
@media (min-width: 1024px){
.social{width:15.25424%
}
}
.social--header-white{color:white;white-space:nowrap;display:block
}
.social--icons{height:2em;display:-ms-flexbox;display:flex;-ms-flex-pack:justify;justify-content:space-between;-ms-flex-flow:row nowrap;flex-flow:row nowrap
}
.social--icons-white{width:2em;fill:white
}



