
@media screen and (min-width: 320px) and (max-width: 767px) and (orientation: landscape){
html{display:none
}
}
body{font-family:'roboto_condensedregular', 'Helvetica Neue', Helvetica, Arial, sans-serif;margin:0
}
h1{font-size:36px;font-weight:300;width:10em;margin:1em auto;text-align:center;color:#4a4a4a;border:2px solid black
}
h2{font-size:30px;font-weight:300;text-align:center;color:#4a4a4a
}
h3{font-size:24px;font-weight:300;color:#4a4a4a
}
h4{font-size:20px;font-weight:300;color:#4a4a4a
}
a{text-decoration:none
}
.video-banner{width:70em;position:relative;margin:0 auto;height:39.375em;overflow:hidden;margin-bottom:1.69492%
}
@media (max-width: 648px){
.video-banner{display:none
}
}
.video-banner-video{position:absolute;top:-5%;left:-5%;width:110%;height:110%
}
.video-banner-thumbnail{width:60%;position:absolute;left:0;right:0;margin:0 auto
}
.items{width:100%;display:-ms-flexbox;display:flex;-ms-flex-pack:distribute;justify-content:space-around;-ms-flex-flow:row wrap;flex-flow:row wrap
}
@media (min-width: 649px){
.items{width:49.15254%;margin-left:25.42373%
}
}
.items--item{margin-bottom:1.69492%
}
@media (min-width: 649px){
.items--item{width:49.15254%
}
}
@media (min-width: 1024px){
.items--item{width:32.20339%
}
}
.items--item img{width:100%;height:100%;object-fit:cover
}
.overlay-title{position:absolute;bottom:13px;background:black;text-align:center;color:white;margin:0 3%;z-index:5;padding:0 5px;-webkit-backface-visibility:hidden;-webkit-perspective:1000
}
@media (min-width: 1024px){
.overlay-title{font-size:24px
}
}



