
@media screen and (min-width: 320px) and (max-width: 767px) and (orientation: landscape){
html{display:none
}
}
body{font-family:'roboto_condensedregular', 'Helvetica Neue', Helvetica, Arial, sans-serif;margin:0
}
h1{font-size:36px;font-weight:300;width:10em;margin:1em auto;text-align:center;color:#4a4a4a;border:2px solid black
}
h2{font-size:30px;font-weight:300;text-align:center;color:#4a4a4a
}
h3{font-size:24px;font-weight:300;color:#4a4a4a
}
h4{font-size:20px;font-weight:300;color:#4a4a4a
}
a{text-decoration:none
}
.header{position:relative;width:100%;background:white;display:inline-block
}
.header--logo{position:absolute;bottom:0;width:40.67797%;float:left;margin-right:1.69492%;padding:1.69492%
}
@media (min-width: 649px){
.header--logo{position:relative;width:6.77966%;float:left;margin-right:1.69492%;margin-left:6.77966%
}
}
.header--burger{content:url("./assets/content/burger--dark.png");padding:1em;width:10.16949%;float:right;margin-right:0;margin-left:33.89831%
}
@media (min-width: 649px){
.header--burger{display:none
}
}
@media (max-width: 1023px){
.navigation{display:none;position:absolute;height:88vh;top:4.2vh;background:#ddd;z-index:1000;width:100%;float:left;margin-left:0;margin-right:0;display:none;-ms-flex-flow:column nowrap;flex-flow:column nowrap;-ms-flex-pack:distribute;justify-content:space-around
}
.navigation--link{text-align:center;font-weight:bold;border-bottom:1px solid black
}
.toggleNav>.navigation{display:-ms-flexbox;display:flex
}
.toggleNav>.header--burger{content:url("./assets/content/burger--light.png");background:#ddd
}
}
@media (min-width: 1024px){
.navigation{width:49.15254%;float:left;margin-right:1.69492%;margin-left:25.42373%;display:-ms-flexbox;display:flex;-ms-flex-flow:row nowrap;flex-flow:row nowrap;-ms-flex-pack:distribute;justify-content:space-around
}
.navigation__element{border-bottom:2px solid transparent
}
.navigation__element:hover{border-bottom:2px solid black
}
}



