
@media screen and (min-width: 320px) and (max-width: 767px) and (orientation: landscape){
html{display:none
}
}
body{font-family:'roboto_condensedregular', 'Helvetica Neue', Helvetica, Arial, sans-serif;margin:0
}
h1{font-size:36px;font-weight:300;width:10em;margin:1em auto;text-align:center;color:#4a4a4a;border:2px solid black
}
h2{font-size:30px;font-weight:300;text-align:center;color:#4a4a4a
}
h3{font-size:24px;font-weight:300;color:#4a4a4a
}
h4{font-size:20px;font-weight:300;color:#4a4a4a
}
a{text-decoration:none
}
.team-container{display:-ms-flexbox;display:flex;-ms-flex-direction:row;flex-direction:row;-ms-flex-wrap:wrap;flex-wrap:wrap;-ms-flex-pack:distribute;justify-content:space-around;-ms-flex-line-pack:center;align-content:center
}
.member{overflow:hidden
}
.member img{width:15em
}
.member img:hover{transform:scale(1.1);transition-duration:140ms
}
@media (min-width: 1024px){
.member img{width:25em
}
}
.sponsors-container{display:-ms-flexbox;display:flex;-ms-flex-direction:row;flex-direction:row;-ms-flex-wrap:wrap;flex-wrap:wrap;-ms-flex-pack:distribute;justify-content:space-around;-ms-flex-line-pack:center;align-content:center
}
.sponsor .img{width:300px;box-sizing:border-box
}
.sponsor .img:hover{transform:scale(1.1);transition-duration:140ms
}



